.Loader_loader__1GKNf{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px dashed teal;
    /* линейная и бесконечная функция */
    -webkit-animation: Loader_rotate__34E9H 1s infinite linear;
            animation: Loader_rotate__34E9H 1s infinite linear;
}

/* ключевые кадры keyframes 
   rotate - название*/
@-webkit-keyframes Loader_rotate__34E9H {
    /* начало анимации */
    from{
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    /* конец анимации */
    to{
        -webkit-transform: rotate(360deg) scale(1.4);
                transform: rotate(360deg) scale(1.4);
    }
}
@keyframes Loader_rotate__34E9H {
    /* начало анимации */
    from{
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    /* конец анимации */
    to{
        -webkit-transform: rotate(360deg) scale(1.4);
                transform: rotate(360deg) scale(1.4);
    }
}
